import React from 'react';
import CreditPriceMultipleBarChart from '../creditPriceMultipleBarChart';
import CreditAmountBarChart from '../creditAmountBarChart';

export default function FinanceBeta() {
    return (
      <div>
        <div className="row">
          <div className="col-lg-12 col-xl-6">
            <div className="card">
              <div className="card-header">
                <div className="utils__title">
                  <strong>Hc ph tn ch theo i tng v h</strong>
                </div>
                <div className="utils__titleDescription" />
              </div>
              <div className="card-body">
                <div className="row">
                  <div className="col-xl-12">
                    <CreditPriceMultipleBarChart />
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div className="col-lg-12 col-xl-6">
            <div className="card">
              <div className="card-header">
                <div className="utils__title">
                  <strong>Tin hc ph tn ch theo h</strong>
                </div>
                <div className="utils__titleDescription" />
              </div>
              <div className="card-body">
                <div className="row">
                  <div className="col-xl-12">
                    <CreditAmountBarChart />
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }



