import React, { Component, Fragment } from 'react';
import { BarChart, Bar, XAxis, YAxis, CartesianGrid, Tooltip, Legend } from 'recharts';
import { getChartCreditPriceByFeeObjectNTrainType } from '../../../services/chartFinance';
import CustomizedAxisTick from './CustomizedAxisTick';

const COLORS = ['#0088FE', '#00C49F', '#FFBB28', '#FF8042'];

export default class CreditPriceMultipleBarChart extends Component {
  state = {
    creditPriceChartData: [],
    widthChart: 500,
  };

  componentDidMount = async () => {
    const widthChart = document.getElementsByClassName('utils__title')[0].offsetWidth-30;
    this.setState({
      widthChart,
    });
    const res = await getChartCreditPriceByFeeObjectNTrainType();
    const formatedData = res.data.labels.map((label, index) => {
      const item = { name: label };
      res.data.datas.forEach((data) => {
        item[data.label] = data.values[index];
      });
      return item;
    });
    this.setState({
      creditPriceChartData: formatedData,
    });
  };

  render() {
    const { creditPriceChartData, widthChart } = this.state;
    console.log('creditPriceChartData',creditPriceChartData);
    return (
      <Fragment>
        <BarChart
          width={widthChart}
          height={300}
          data={creditPriceChartData}
          margin={{
            top: 20,
            right: 30,
            left: 20,
            bottom: 5,
          }}
        >
          <CartesianGrid />
          <XAxis dataKey="name" height={60} tick={<CustomizedAxisTick />} />
          <YAxis />
          <Tooltip />
          <Legend />
          {Object.keys(creditPriceChartData[0] || {}).map((key, index) => {
            console.log('key',key);
            if (key === 'name') return null;
            return <Bar key={Math.floor(Math.random() * 100) + 1} dataKey={key} fill={COLORS[index]} />;
          })}
        </BarChart>
      </Fragment>
    );
  }
}



